import React, { useState } from "react"
import { useStaticQuery, graphql } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import MenuItem from "../../components/menuitem.js"

import { Icon, Row, Col } from "react-materialize"

const ALaKarte_Menu_Page = () => {
  const images = useStaticQuery(graphql`
    query {
      steamed_soybeans: file(
        relativePath: { eq: "a la karte/steamed_soybeans.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      potato_salad: file(relativePath: { eq: "a la karte/potato_salad.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      ginger_grilled_pork: file(
        relativePath: { eq: "a la karte/ginger_grilled_pork.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      teriyaki_chicken: file(
        relativePath: { eq: "a la karte/teriyaki_chicken.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      hamburger_steak: file(
        relativePath: { eq: "a la karte/hamburger_steak.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      tempura_platter: file(
        relativePath: { eq: "a la karte/tempura_platter.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      fried_chicken_ponzu: file(
        relativePath: { eq: "a la karte/fried_chicken_ponzu.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      bacon_ceasar_salad: file(
        relativePath: { eq: "a la karte/bacon_ceasar_salad.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      beef_steak: file(relativePath: { eq: "a la karte/beef_steak.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      grilled_silver_ladle: file(
        relativePath: { eq: "a la karte/grilled_silver_ladle.jpg" }
      ) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  const [numTotalItems, setItems] = useState(0)

  const addItem = () => {
    setItems(numTotalItems + 1)
  }

  const removeItem = () => {
    setItems(numTotalItems - 1)
  }

  return (
    <Layout>
      <SEO title="Online Ordering" />
      <div style={{ marginTop: "4%", marginLeft: "10%", width: "80%" }}>
        <Row>
          <Col s={3}>
            <MenuItem
              key={"1"}
              title="Steamed Soybeans"
              price={5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.steamed_soybeans.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"2"}
              title="Vegetable Salad"
              price={8}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.bacon_ceasar_salad.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"3"}
              title="Potato Salad"
              price={6}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.potato_salad.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"4"}
              title="Bacon Ceasar Salad"
              price={10}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.bacon_ceasar_salad.childImageSharp.fluid}
            />
          </Col>
        </Row>
        <Row>
          <Col s={4}>
            <MenuItem
              key={"5"}
              title="Tempura Platter"
              price={18}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tempura_platter.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"6"}
              title="Fried Chicken Ponzu"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.fried_chicken_ponzu.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"7"}
              title="Grilled Salmon"
              price={20}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.fried_chicken_ponzu.childImageSharp.fluid}
            />
          </Col>
        </Row>
        <Row>
          <Col s={4}>
            <MenuItem
              key={"8"}
              title="Grilled Silver Ladle"
              price={25}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.grilled_silver_ladle.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"9"}
              title="Ginger Grilled Pork"
              price={13}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.ginger_grilled_pork.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"10"}
              title="Terriyaki Chicken"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.teriyaki_chicken.childImageSharp.fluid}
            />
          </Col>
        </Row>
        <Row>
          <Col s={4}>
            <MenuItem
              key={"11"}
              title="Hamburger Steak"
              price={18}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.hamburger_steak.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"12"}
              title="Beef Steak"
              price={25}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.beef_steak.childImageSharp.fluid}
            />
          </Col>
        </Row>
      </div>
    </Layout>
  )
}

export default ALaKarte_Menu_Page
